body{
    overflow-x: hidden;
    #root{
        .App{
            width: 100vw;
            height: auto;
            box-sizing: border-box;
            background-color: #5d00ff;
        }
    }
}
body::-webkit-scrollbar {
    width: 12px;
}
body::-webkit-scrollbar-track {
    background: #ECDFC3;
}

body::-webkit-scrollbar-thumb {
    background-color: #2B7F89;
    border-radius: 20px;
    border: 3px solid #ECDFC3;
}